import '@/assets/js/hm.js';


export default {
  name: "IndexView",
  components: {Layout},
  data() {
    return {
      tabList: [
        {name: '', desc: '', icon: 'am-icon-cog'},
        {name: '', desc: '', icon: 'am-icon-lightbulb-o'},
        {name: '', desc: '', icon: 'am-icon-line-chart'},
        {name: '', desc: '', icon: 'am-icon-hourglass-end'},
      ],
      tabIndex: 0,
      slideshow: [],

      advantageList: [
        {
          id: 1,
          cover: require('../assets/images/index/f01.jpg'),
          title: '',
          desc: '600300'
        },
        {
          id: 2,
          cover: require('../assets/images/index/f01.jpg'),
          title: '',
          desc: 'ISO9001ISO14000ISO45001BSCICCCCBGSUL&CULCERoHSRECAHPSE'
        },
        {
          id: 3,
          cover: require('../assets/images/index/f01.jpg'),
          title: '',
          desc: '17'
        },
        {
          id: 4,
          cover: require('../assets/images/index/f01.jpg'),
          title: ' ',
          desc: '20211919.632021202545.6%2030118.1%'
        },
      ],
      serviceList: [
        {
          id: 1,
          icon: 'am-icon-diamond',
          title: '',
          desc: ''
        },
        {
          id: 2,
          icon: 'am-icon-user',
          title: '',
          desc: ''
        },
        {
          id: 3,
          icon: 'am-icon-umbrella',
          title: '',
          desc: ''
        },
        {
          id: 4,
          icon: 'am-icon-briefcase',
          title: '',
          desc: ''
        }
      ]
    }
  },
  beforeCreate() {
    var cur_url = window.location.href.split("/#/")[0]
// console.log('iiiiiiiii')
//     console.log(cur_url)


    var mobileUrl = cur_url+"/#/index_m";
    var ua = navigator.userAgent;
    var ipad = ua.match(/(iPad).*OS\s([\d_]+)/),
        isIphone = !ipad && ua.match(/(iPhone\sOS)\s([\d_]+)/),
        isAndroid = ua.match(/(Android)\s+([\d.]+)/),
        isMobile = isIphone || isAndroid;
//
    if (isMobile||ipad) {
      // console.log('')
      // console.log(mobileUrl)
      this.$router.push('/index_m')
      // window.location.href = mobileUrl;
      // 
      // window.location.href = mobileUrl;
    }
  },
  mounted() {

    this.getSlideshow()


    $(document).ready(function () {


      (function($) {
        $.fn.Slider = function(options) {
          "use strict";

          var settings = $.extend({
            isAuto: true,
            transTime: 4000,
            animateSpeed: 1000,
            sliderMode: 'slide', //'slide | fade',
            pointerControl: true,
            pointerEvent: 'click',//'hover' | 'click',
            arrowControl: true,
          }, options);
          var interval;
          var isAnimating     = false;
          var $slider         = $(this);
          var $sliderWrap     = $slider.find('.slider-inner');
          var sliderCount     = $sliderWrap.find('> .item').length;
          var sliderWidth     = $slider.width();
          var currentIndex    = 0;
          var mouseType       = true;
          var sliderFun = {
            controlInit: function() {
              // pointerControl
              if (settings.pointerControl) {

                var html = '';
                html += '<ol class="slider-pointer">';
                for (var i = 0; i < sliderCount; i++) {
                  if (i == 0) {
                    html += '<li class="active"></li>'
                  }else{
                    html += '<li></li>'
                  }
                }
                html += '</ol>'
                $slider.append(html);
                // 
                var $pointer = $slider.find('.slider-pointer');
                $pointer.css({
                  left: '50%',
                  marginLeft: - $pointer.width()/2
                });
              }
              // pointerControl
              if (settings.arrowControl) {
                var html = "";
                html += '<span class="slider-control prev"></span>';
                html += '<span class="slider-control next"></span>'
                $slider.append(html);
              }
              $slider.on('click', '.slider-control.prev', function(event) {
                sliderFun.toggleSlide('prev');
              });
              $slider.on('click', '.slider-control.next', function(event) {
                sliderFun.toggleSlide('next');
              });
            },
            // slider
            sliderInit: function() {
              sliderFun.controlInit();
              // 
              if (settings.sliderMode == 'slide') {
                // slide 
                $sliderWrap.width(sliderWidth * sliderCount);
                $sliderWrap.children().width(sliderWidth);
              }else{
                // mode 
                $sliderWrap.children().css({
                  'position': 'absolute',
                  'left': 0,
                  'top': 0
                });
                $sliderWrap.children().first().siblings().hide();
              }
              // 
              if (settings.pointerEvent == 'hover') {
                $slider.find('.slider-pointer > li').mouseenter(function(event) {
                  sliderFun.sliderPlay($(this).index(),"stop");
                });
              }else{
                $slider.find('.slider-pointer > li').click(function(event) {
                  if (currentIndex != $(this).index()) {
                    sliderFun.sliderPlay($(this).index(),"stop")
                  }
                });
              }
              // 
              sliderFun.autoPlay();
              $slider.on("mouseenter",function () {
                mouseType = false;
                // if(interval){
                sliderFun.stop();
                // }

              });
              $slider.on("mouseleave",function () {
                mouseType = true;
                sliderFun.autoPlay();
              })
            },
            // slidePlay
            sliderPlay: function(index,stop) {
              sliderFun.stop();
              isAnimating = true;
              $sliderWrap.children().first().stop(true, true);
              $sliderWrap.children().stop(true, true);
              $slider.find('.slider-pointer').children()
                  .eq(index).addClass('active')
                  .siblings().removeClass('active');
              if (settings.sliderMode == "slide") {
                // slide
                if (index > currentIndex) {
                  $sliderWrap.animate({
                    left: '-=' + Math.abs(index - currentIndex) * sliderWidth + 'px'
                  }, settings.animateSpeed, function() {
                    sliderFun.stop();
                    isAnimating = false;
                    if(!stop&&mouseType){
                      sliderFun.autoPlay()
                    }


                  });
                } else if (index < currentIndex) {
                  $sliderWrap.animate({
                    left: '+=' + Math.abs(index - currentIndex) * sliderWidth + 'px'
                  }, settings.animateSpeed, function() {
                    isAnimating = false;
                    if(!stop&&mouseType){
                      sliderFun.autoPlay()
                    }
                  });
                } else {
                  return;
                }
              }else{
                // fade
                if ($sliderWrap.children(':visible').index() == index) return;
                $sliderWrap.children().fadeOut(settings.animateSpeed)
                    .eq(index).fadeIn(settings.animateSpeed, function() {
                  isAnimating = false;
                  if(!stop&&mouseType){
                    sliderFun.autoPlay()
                  }
                });
              }
              currentIndex = index;
            },
            // toggleSlide
            toggleSlide: function(arrow) {
              if (isAnimating) {
                return;
              }
              var index;
              if (arrow == 'prev') {
                index = (currentIndex == 0) ? sliderCount - 1 : currentIndex - 1;
                sliderFun.sliderPlay(index,"stop");
              }else if(arrow =='next'){
                index = (currentIndex == sliderCount - 1) ? 0 : currentIndex + 1;
                sliderFun.sliderPlay(index,"stop");
              }
            },
            // autoPlay
            autoPlay: function() {
              if (settings.isAuto) {
                interval = setInterval(function () {
                  var index = currentIndex;
                  (currentIndex == sliderCount - 1) ? index = 0: index = currentIndex + 1;
                  sliderFun.sliderPlay(index);
                }, settings.transTime);
              }else{
